form {
    max-width: 380px;
    margin: 0 auto;
    margin-top: 50px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f7f7f7;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  form label {
    display: block;
    margin-bottom: 5px;
    text-align: left;
    color: #666;
    font-size: 14px;
  }
  
  form input[type="email"],
  form input[type="password"] {
    width: 100%;
    height: 30px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    color: #555;
  }
  
  form button {
    display: block;
    width: 100%;
    padding: 10px;
    margin-top: 20px;
    border: none;
    border-radius: 5px;
    background-color: #0070f3;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
  }
  
  form button:hover {
    background-color: #005bd1;
  }
  
  form a {
    display: block;
    margin-top: 20px;
    text-align: center;
    color: #333;
    font-size: 14px;
    text-decoration: none;
  }
  
  form a:hover {
    text-decoration: underline;
  }
  
  form p {
    color: red;
    margin-top: 10px;
    font-size: 14px;
  }